import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import EgyptGuide from './Guides/Africa/EgyptGuide.js';
import SouthAfricaGuide from './Guides/Africa/SouthAfricaGuide.js';
import MoroccoGuide from './Guides/Africa/MoroccoGuide.js';

import ChinaGuide from './Guides/Asia/ChinaGuide.js';
import JapanGuide from './Guides/Asia/JapanGuide.js';
import TurkeyGuide from './Guides/Asia/TurkeyGuide.js'; 

class SeeDest extends React.Component {
  render() {
    return (
      <Router>
        <Switch>
          <div>  
            <Route component={EgyptGuide} path="/seedestinations/egyptguide" />
            <Route component={SouthAfricaGuide} path="/seedestinations/southafricaguide" />
            <Route component={MoroccoGuide} path="/seedestinations/moroccoguide" />

            <Route component={ChinaGuide} path="/seedestinations/chinaguide" />
            <Route component={JapanGuide} path="/seedestinations/japanguide" />
            <Route component={TurkeyGuide} path="/seedestinations/turkeyguide" />
          </div>
        </Switch> 
      </Router>
    );
  }
}

export default SeeDest